body {
  font-family: 'Montserrat', sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3, h4, h5, h6, p, span, tr, th, td, div, ul, li, input, label, textarea {
  font-family: 'Montserrat', sans-serif;
}

hr {
  margin-top: 6px !important;
  margin-bottom: 6px !important;
}

.height-50 {
  height: 50px !important;
}

.font-12 {
  font-size: 12px !important;
}

.font-15 {
  font-size: 15px !important;
}

.font-18 {
  font-size: 18px !important;
}

.font-19 {
  font-size: 19px !important;
}

.font-21 {
  font-size: 21px !important;
}

.font-22 {
  font-size: 22px !important;
}

.font-24 {
  font-size: 24px !important;
}

.font-36 {
  font-size: 36px !important;
  font: bold;
}

.font-bold {
  font-weight: bold !important;
}

.cursor-pointer {
  cursor: pointer;
}

.float-right {
  float: right;
}

.sidebar {
  min-height: 100vh;
  border-right: 1px solid #BBBBBB;
}

.signoutLink {
  position: fixed;
  bottom: 20px;
  width: 16.666667% !important;
}

@media (max-width: 768px) {
  .sidebar {
    min-height: inherit;
  }
  .signoutLink {
    position: inherit;
    margin-top: 30px;
    margin-bottom: 10px;
    width: 100% !important;
  }
  .font-36 {
    font-size: 30px !important;
  }
}

.menuItem {
  font-size: 18px;
  color: #000000;
}

.menuItem:hover {
  background-color: rgba(61, 189, 143, 0.4);
  border-radius: 10px;
  cursor: pointer;
}

.selected {
  background-color: rgba(61, 189, 143, 0.4);
}

.top-border {
  border-top: 1px solid #aaaaaa;
}

.bg-login {
  background-color: #3DBD8F;
  min-height: 100vh;
}

.bg-login .form-label {
  text-decoration: none;
  color: white;
  float: right;
}

.bg-keto-primary {
  background-color: rgba(61, 189, 143, 0.4);
}

.bg-keto-secondary {
  background-color: #F5F7FB;
}

.color-keto-secondary {
  color: #929292;
}

.color-keto-primary {
  color: #3DBD8F;
}

.table {
  font-size: 13px !important;
  margin-bottom: 0px !important;
}

.admin-dashboard th, .admin-dashboard td {
  padding: 2px !important;
}

table tbody tr td {
  vertical-align: middle !important;
}

.keto-shadow {
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.keto-rounded-lg {
  border-radius: 10px !important;
}

.admin-affiliates .table {
  border-left: 1px solid lightgray;
  border-right: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}

.badge {
  padding: 5px;
  border-radius: 4px;
}

.badge-keto-primary {
  border: 1px solid #3DBD8F !important;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)), #3DBD8F;
  color: #3DBD8F;
}

.badge-keto-warning {
  border: 1px solid #FBD13E !important;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)), #FBD13E;
  color: #FBD13E;
}

.badge-keto-danger {
  border: 1px solid #FF0000 !important;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)), #FF0000;
  color: #FF0000;
}

.admin-products th, .admin-products td {
  padding: 2px !important;
}

.product-image-border {
  border-radius: 5px !important;
}

.store-image {
  width: 100%;
  aspect-ratio: 4/3;
}

.admin-create-product .product-field {
  padding-top: 1px;
  padding-bottom: 1px;
  height: 30px;
}

.file {
  height: 1px;
  width: 10px;
  margin-left: 50px;
  margin-top: -2px;
  border: 1px solid transparent;
}

.product-description {
  -webkit-user-modify: read-write-plaintext-only;
  line-height: 30px;
}

.input-group-keto-small {
  width: 122px !important;
}

.input-keto-small {
  text-align: right;
}

.btn-keto-light {
  background-color: lightgray;
}

.client-cart hr {
  margin: 0px !important;
}

.no-cartlines {
  text-align: center;
  margin: 100px 0;
}

.narrow-input {
  padding-top: 1px;
  padding-bottom: 1px;
  height: 30px;
}

.btn-register {
  background: rgba(255, 255, 255, 0.7);
  width: 250px;
}

.material-icons {
  cursor: pointer;
  margin-left: 10px;
  margin-right: 10px;
}

.search-filter {
  width: 120px;
  margin-left: 10px;
  height: 25px;
}

.bg-affiliate {
  min-height: 100vh;
}

.min-width-250 {
  min-width: 250px;
}

.hover-keto-light:hover {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)), #C2C9D1;
}

.course-description {
  -webkit-user-modify: read-write-plaintext-only;
}